footer {
  width: 100%;
  background-color: #2c3e50;
  color: white;
  text-align: center;
  padding: 15px 0;
  position: relative;
}

footer p {
  margin: 0;
  font-size: 14px;
}

footer a {
  color: #f39c12;
  text-decoration: none;
}

footer a:hover {
  text-decoration: underline;
}
